.join-box {
  width: 512px;
  margin: 0 auto;
  margin-top: 200px;
  border-radius: 4px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  .message {
    &.success {
      color: rgb(86, 193, 86);
    }
    &.error {
      color: rgb(203, 74, 74);
    }
  }
}

.login-box {
  width: 512px;
  margin: 0 auto;
  margin-top: 200px;
  border-radius: 4px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.join-title {
  background-color: #b986ea;
  color: rgb(226, 225, 225);
  text-align: center;
  border: 1px solid #b986ea;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.input-email {
  border: 1px solid #b986ea;
  border-radius: 4px;
  margin-top: 10px;
  padding: 10px;
}

.input-password {
  border: 1px solid #b986ea;
  border-radius: 4px;
  margin-top: 10px;
  padding: 10px;
}

.join-btn {
  margin-top: 30px;
  padding: 10px;
  border: 1px solid #b986ea;
  background-color: #b394cf;
}
